import { HttpClient } from '@angular/common/http';
import { Injectable } from '@angular/core';
import { forkJoin } from 'rxjs';
import { dataNodeEndpoints } from 'src/app/configurations';

@Injectable({
  providedIn: 'root',
})
export class CategoriesDataService {
  constructor(protected httpClient: HttpClient) {}

  pingCategoriesDataProviders(endPoints: string[]) {
    const endpoints = endPoints.map((e) => {
      return this.httpClient.get([e, dataNodeEndpoints.ping].join('/'));
    });
    return forkJoin(endPoints);
  }

  getCategoriesRootVolumeData(endPoint: string) {
    return this.httpClient.get(
      [endPoint, dataNodeEndpoints.checkout, 'root_volume', 'root_volume'].join(
        '/'
      )
    );
  }
}
